.panel_part {
  width: 320px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 6px 12px 0 rgba(29, 118, 187, 0.1);
  background-color: #1d76bb;
  padding: 20px 10px 0;

  .img_wrapper {
    width: 100%;
    padding: 0px 38px;
    margin-bottom: 40px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .avatar_wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding: 9px 0 9px 12px;
    border-radius: 5px;
    background: #ffffff;

    .location {
      margin-left: 16px;
      h3{
        font-family: Rajdhani;
        font-size: 18px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.67;
        letter-spacing: normal;
        text-align: left;
        color: var(--pure-black);
        margin: 0 0 14px 0 !important;
      }
      div{
        font-family: Rajdhani;
        font-size: 14px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 2.14;
        letter-spacing: normal;
        text-align: left;
        color: #52575d;
      }
    }

    .ant-avatar {
      padding: 3px;
      background: white;

      img {
        border: 1px solid #f69226;
        border-radius: 40px;
      }
    }
  }
}